import React, { Suspense, useEffect, useState } from 'react'
import styled from 'styled-components'
import Loader from 'react-loader-spinner'
import { useSelector } from 'react-redux'
import Box from 'components/Box'
import { CardDetailModal, useModal } from 'components/Modal'
import SuspenseImg from 'components/SuspenseImg'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Pagination from 'components/Pagination'
import PageLoading from 'components/PageLoading'
import { useAppDispatch } from 'state'
import { selectCyblocList, selectCyblocLoading, selectTotalCybloc, setSelectedNftId } from 'state/reducers/cybloc'
import NFTDetail from '../Inventory/NFTDetail'
import useGetCyblocList from './hooks/useGetCyblocList'

const Wrapper = styled(Box)`
  background-color: #0f0a18;
  min-height: inherit;
`

const StyledHeaderText = styled(Text)`
  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;

  @media (max-width: 600px) {
    padding: 0px;
  }
`

const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 800px) {
    max-width: 80%;
  }

  @media (max-width: 576px) {
    max-width: 350px;
  }
`
const Grid = styled(Box)`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 40px;
  padding-bottom: 30px;
  row-gap: 40px;

  @media (max-width: 1100px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
`

const Empty = styled(Box)`
  width: 100%;
  display: flex;
  justify-content: center;
`

const CyblocImage = styled(SuspenseImg)`
  max-width: 100%;
  transition: all 0.3s ease-out;

  &:hover {
    z-index: 1;
    transform: scale(1.05);
    cursor: pointer;
  }
`
const LoaderBox = styled(Flex)`
  flex: 1;
  width: 200px;
  height: 220px;
  justify-content: center;
  align-items: center;
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const TotalCyblocText = styled.span`
  font-weight: 700;
  color: #f3f3f3;
  font-size: 1.2rem;
  opacity: 0.6;
`

const DEFAUL_CYBLOCS_PER_PAGE = 8

const MyCyblocs = () => {
  const [onHandleRefetchingNftList] = useGetCyblocList()
  const [isLoading, setIsLoading] = useState(true)
  const dispatch = useAppDispatch()
  const cyblocList = useSelector(selectCyblocList)
  const totalCyblocs = useSelector(selectTotalCybloc)
  const [currentPage, setCurrentPage] = useState(1)
  const [currentCybloc, setCurrentCybloc] = useState([])
  const isLoadingCyblocList = useSelector(selectCyblocLoading)
  const [onPresentViewCyBlocDetailModal, onDimiss] = useModal(
    <CardDetailModal>
      <NFTDetail
        onRefetching={onHandleRefetchingNftList}
        onDismiss={() => {
          onDimiss()
        }}
      />
    </CardDetailModal>,
  )

  useEffect(() => {
    if (!isLoadingCyblocList) {
      setIsLoading(false)
    }
  }, [isLoadingCyblocList])

  useEffect(() => {
    const cyblocs = cyblocList.slice((currentPage - 1) * DEFAUL_CYBLOCS_PER_PAGE, currentPage * DEFAUL_CYBLOCS_PER_PAGE)
    setCurrentCybloc(cyblocs)
  }, [currentPage])

  return (
    <Wrapper>
      <Row>
        <StyledHeaderContent>
          <Flex alignItems="baseline">
            <StyledHeaderText mr="15px" isActive>
              CyBlocs <TotalCyblocText>{cyblocList.length} CyBloc(s)</TotalCyblocText>
            </StyledHeaderText>
          </Flex>
        </StyledHeaderContent>
        {isLoadingCyblocList || isLoading ? (
          <PageLoading />
        ) : cyblocList.length === 0 ? (
          <Empty>
            <StyledHeaderText>No CyBlocs to display</StyledHeaderText>
          </Empty>
        ) : (
          <>
            <Grid>
              {currentCybloc.map(({ tokenId, image }) => {
                return (
                  <Suspense
                    fallback={
                      <LoaderBox>
                        <Loader type="Oval" color="#007BFF" height={32} width={32} />
                      </LoaderBox>
                    }
                  >
                    <CyblocImage
                      onClick={() => {
                        dispatch(setSelectedNftId(tokenId))
                        onPresentViewCyBlocDetailModal()
                      }}
                      src={image}
                      loading="lazy"
                      alt="image"
                      color="white"
                    />
                  </Suspense>
                )
              })}
            </Grid>
            <Pagination
              currentPage={currentPage}
              totalCount={totalCyblocs}
              pageSize={DEFAUL_CYBLOCS_PER_PAGE}
              onPageChange={(page) => setCurrentPage(page)}
            />
          </>
        )}
      </Row>
    </Wrapper>
  )
}

export default MyCyblocs
