/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  cyblocList: [],
  totalCyblocs: 0,
  newCyblocList: [],
  selectedNftId: null,
  selectedPackId: null,
  isLoading: true,
}

export const cyblocSlice = createSlice({
  name: 'Cybloc',
  initialState,
  reducers: {
    setCyblocList: (state, action) => {
      state.cyblocList = action.payload
    },
    setTotalCybloc: (state, action) => {
      state.totalCyblocs = action.payload
    },
    setNewCylocList: (state, action) => {
      state.newCyblocList = action.payload
    },
    setIsLoadingCybloc: (state, action) => {
      state.isLoading = action.payload
    },
    setSelectedNftId: (state, action) => {
      state.selectedNftId = action.payload
    },
    setSelectedPackId: (state, action) => {
      state.selectedPackId = action.payload
    },
  },
})

// Actions
export const {
  setCyblocList,
  setNewCylocList,
  setIsLoadingCybloc,
  setSelectedNftId,
  setSelectedPackId,
  setTotalCybloc,
} = cyblocSlice.actions
// Selectors
export const selectCyblocList = (state) => state.cybloc.cyblocList
export const selectNewCyblocList = (state) => state.cybloc.newCyblocList
export const selectCyblocLoading = (state) => state.cybloc.isLoading
export const selectSelectedNftId = (state) => state.cybloc.selectedNftId
export const selectSelectedPackId = (state) => state.cybloc.selectedPackId
export const selectTotalCybloc = (state) => state.cybloc.totalCyblocs

export default cyblocSlice.reducer
